import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { viewerType } from 'ngx-doc-viewer/document-viewer.component';
import { environment } from 'src/environments/environment';
import { XmlViewerService } from './xml-viewer.service';

const getbaseUrl = (): string => {
  const pathArray = window.location.href.split('/');
  const protocol = pathArray[0];
  const host = pathArray[2];
  return protocol + '//' + host;
};

@Component({
  selector: 'app-xml-viewer',
  templateUrl: './xml-viewer.component.html',
  styleUrls: ['./xml-viewer.component.css'],
})
export class XmlViewerComponent implements OnInit {

  viewer: {
    name: viewerType;
    doc: string;
    custom: boolean;
    acceptedUploadTypes: string;
    viewerUrl?: string;
  } = {
    name: 'office',
    // 'https://file-examples-com.github.io/uploads/2017/02/file_example_XLSX_50.xlsx'
    doc: '',
    custom: true,
    acceptedUploadTypes: null,
  };

  selectedViewer = null;
  selectedDoc = null;

  constructor(
    private readonly xmlService: XmlViewerService,
    private readonly route: ActivatedRoute,
    private readonly location: Location
  ) {}

  ngOnInit(): void {
    this.route.paramMap.subscribe((param) => {
      if (param.has('id')) {
        const id = +param.get('id');
        this.xmlService.documentList.subscribe(lists => {
          const data =  lists.find((doc) => doc.id === id);
          if (data) {
            this.viewer.doc = environment.baseApi + '/' + data.file_path;
            this.viewer.doc = environment.baseApi + `/download/excel/${id}`;
            this.selectedViewer = this.viewer;
            this.selectedDoc = this.selectedViewer.doc;
          } else {
            this.location.back();
          }
        });
      } else {
        // this.location.back();
      }
    });
  }

}
